import _ from 'lodash';
import store from '../store';
import { getResourceById } from '../utils/resourceTypes';

const { DS_HOST, DS_PORT } = window;
const API_NAMESPACE = 'dadmin/v1';

export function generateApiUrl({
  host = DS_HOST,
  port = DS_PORT,
  namespace = API_NAMESPACE,
  endpoint,
}) {
  if (host && port) {
    return `https://${host}:${port}/${namespace}/${endpoint}`;
  }

  return `/${namespace}/${endpoint}`;
}

export function userEndpoint(userId, options = {}) {
  if ('password' in options && options.password === true) {
    return generateApiUrl({ endpoint: `users/${userId}/password` });
  }

  return generateApiUrl({ endpoint: `users/${userId}` });
}

export function groupEndpoint(groupId, options = {}) {
  if ('editMembers' in options && options.editMembers === true) {
    return generateApiUrl({ endpoint: `groups/${groupId}/relationships/members` });
  }

  if ('members' in options && options.members === true) {
    return generateApiUrl({ endpoint: `groups/${groupId}/members` });
  }

  return generateApiUrl({ endpoint: `groups/${groupId}` });
}

function getDynamicURL(storePath) {
  return _.get(store.getState(), storePath);
}

function getResourceURL(resourceId, options) {
  const resource = getResourceById(resourceId);
  let endpoint = _.get(resource, 'attributes.resourceEndpoint');
  if (endpoint && options.id) endpoint = `${endpoint}/${options.id}`;
  if (endpoint && options.password) endpoint = `${endpoint}/password`;
  if (endpoint && options.editMembers) endpoint = `${endpoint}/relationships/members`;
  if (endpoint && options.members) endpoint = `${endpoint}/members`;
  return endpoint;
}

export const AUTH_ENDPOINT = generateApiUrl({ endpoint: 'resourceTypes' });
export const USERS_ENDPOINT = generateApiUrl({ endpoint: 'users' });
export const GROUPS_ENDPOINT = generateApiUrl({ endpoint: 'groups' });
export const RESOURCE_TYPES_ENDPOINT = 'resourceTypes';

const endpoints = {
  authenticatedUser: () => getDynamicURL('resourceTypes.links.authUser'),
  endpointForResource: (resourceId, options = {}) => getResourceURL(resourceId, options),
  resourceTypes: () => RESOURCE_TYPES_ENDPOINT,
};

export default endpoints;
