import React, { Component } from 'react';
import { injectIntl, intlShape } from 'react-intl';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Button from 'ui-library/lib/components/buttons/Button';
import HelpHint from 'ui-library/lib/components/tooltips/HelpHint';
import Icon from 'ui-library/lib/components/general/Icon';
import _ from 'lodash';

import { addGroupMemberAction, removeGroupMemberAction } from '../../../store/actions/groups/groups';
import { MODEL_TYPES, PROP_MODEL_TYPES } from '../../../utils/modelTypes';

import './ModifyMemberButton.css';

export class ModifyMemberButton extends Component {
  constructor(props) {
    super(props);

    this.getRelationships = this.getRelationships.bind(this);
    this.filterById = this.filterById.bind(this);
    this.filterByIsDirectMember = this.filterByIsDirectMember.bind(this);
    this.userIsGroup = this.userIsGroup.bind(this);
    this.handleAddGroupMember = this.handleAddGroupMember.bind(this);
    this.handleRemoveGroupMember = this.handleRemoveGroupMember.bind(this);

    this.state = {
      isLoading: false,
    };
  }

  componentDidUpdate() {
    // NOTE: We are ok to use setState here because of the conditional
    // https://reactjs.org/docs/react-component.html#componentdidupdate
    /* eslint-disable react/no-did-update-set-state */
    if (this.state.isLoading) {
      this.setState({ isLoading: false });
    }
  }

  getRelationships() {
    const { profilePageModel, group, user } = this.props;
    if (profilePageModel === MODEL_TYPES.USERS) {
      return _.get(user, 'relationships.groups.data', []);
    }

    const directMembers = _.get(group, 'relationships.members.data.directMembers', []);
    const indirectMembers = _.get(group, 'relationships.members.data.indirectMembers', []);
    return _.concat(directMembers, indirectMembers);
  }

  filterById(relationships) {
    const { profilePageModel, group, user } = this.props;
    if (profilePageModel === MODEL_TYPES.USERS) {
      const id = _.get(group, 'id', undefined);
      return relationships && relationships.filter(r => r.id === id);
    }

    const id = _.get(user, 'id', undefined);
    return relationships && relationships.filter(r => r.id === id);
  }

  filterByIsDirectMember(relationships) {
    const filteredRelationships = this.filterById(relationships);
    return filteredRelationships && filteredRelationships.filter(r => r.isDirectMember);
  }

  userIsGroup() {
    return _.get(this.props.user, 'type', MODEL_TYPES.USERS) === MODEL_TYPES.GROUPS;
  }

  handleAddGroupMember() {
    const { group, user, profilePageModel } = this.props;
    this.props.addGroupMember(group, [user], profilePageModel);
    this.setState({ isLoading: true });
  }

  handleRemoveGroupMember() {
    const { group, user, profilePageModel } = this.props;
    this.props.removeGroupMember(group, [user], profilePageModel);
    this.setState({ isLoading: true });
  }

  render() {
    const { intl, group, addMember } = this.props;
    const { isLoading } = this.state;
    const relationships = this.getRelationships();
    const isRelationship = this.filterById(relationships).length;
    const isDirectMember = this.filterByIsDirectMember(relationships).length;
    const isDisabled = group.attributes['dadmin-is-dynamic-group'][0] || (isRelationship && !isDirectMember);
    let iconName;

    if (isLoading) {
      iconName = undefined;
    } else if (addMember) {
      iconName = 'plus';
    } else {
      iconName = 'minus';
    }

    if (isDisabled) {
      let hintText;

      if (group.attributes['dadmin-is-dynamic-group'][0]) {
        hintText = 'components.modify-member-button.dynamic-group';
      } else {
        hintText = 'components.modify-member-button.indirect-member';
      }

      return (
        <div className="ModifyMemberButton">
          <HelpHint
            className="short-tooltip right"
            hintText={intl.formatMessage({ id: hintText })}
          >
            <Icon
              iconName="help-rounded"
            />
          </HelpHint>
          <Button
            iconName={iconName}
            inline
            disabled
          />
        </div>
      );
    }

    return (
      <div className="ModifyMemberButton">
        <Button
          iconName={iconName}
          inline
          onClick={
            addMember
            ? this.handleAddGroupMember
            : this.handleRemoveGroupMember
          }
          loading={isLoading}
          disabled={isLoading}
        />
      </div>
    );
  }
}

ModifyMemberButton.propTypes = {
  intl: intlShape.isRequired,
  addGroupMember: PropTypes.func.isRequired,
  addMember: PropTypes.bool,
  group: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({
      'dadmin-is-dynamic-group': PropTypes.arrayOf(PropTypes.bool).isRequired,
    }).isRequired,
    relationships: PropTypes.shape({
      members: PropTypes.shape({
        data: PropTypes.shape({
          directMembers: PropTypes.arrayOf(PropTypes.shape({})),
          indirectMembers: PropTypes.arrayOf(PropTypes.shape({})),
        }),
      }),
    }),
  }).isRequired,
  removeGroupMember: PropTypes.func.isRequired,
  user: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }),
    relationships: PropTypes.shape({
      groups: PropTypes.shape({
        data: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
      }),
    }),
  }).isRequired,
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
};

ModifyMemberButton.defaultProps = {
  addMember: true,
};

export default injectIntl(connect(
  null,
  {
    addGroupMember: addGroupMemberAction,
    removeGroupMember: removeGroupMemberAction,
  },
)(ModifyMemberButton));
