import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

import ColumnListItem from './ColumnListItem';
import { getTitleOrPrimaryAttributeValue } from '../../../utils/helpers';
import { PROP_MODEL_TYPES } from '../../../utils/modelTypes';

import './ColumnList.css';

class ColumnList extends Component {
  constructor(props) {
    super(props);

    this.displayItems = this.displayItems.bind(this);
    this.handleScroll = this.handleScroll.bind(this);

    this.state = {
      limit: 100,
    };
  }

  handleScroll(e) {
    const { items } = this.props;
    const { limit } = this.state;
    const bottom = e.target.scrollHeight - e.target.scrollTop === e.target.clientHeight;
    if (bottom && limit <= items.length) {
      this.setState({ limit: limit + 100 });
    }
  }

  displayItems() {
    const {
      addable,
      group,
      items,
      meta,
      user,
      profilePageModel,
    } = this.props;
    const { limit } = this.state;

    if (items.length) {
      return (
        <ul className="items-list">
          {
            items.slice(0, limit).map((item) => {
              // Determine whether it's a group of the same type (not included) because only groups
              // can display other groups in their searches currently.
              const attributeMeta =
                item.type === _.get(group, 'data.type') || item.type === _.get(user, 'data.type')
                  ? meta
                  : meta.included[item.type].attributes;

              const itemName = getTitleOrPrimaryAttributeValue(
                item.attributes,
                attributeMeta,
              );

              return (
                <ColumnListItem
                  key={item.id}
                  addable={addable}
                  item={item}
                  itemName={itemName}
                  user={user}
                  group={group}
                  profilePageModel={profilePageModel}
                  meta={meta}
                />
              );
            })
          }
        </ul>
      );
    }

    return null;
  }

  render() {
    const { addable } = this.props;
    const addableClass = `${addable ? 'addable' : 'added'}`;

    return (
      <div className={`ColumnList ${addableClass}`} onScroll={this.handleScroll}>
        {this.displayItems()}
      </div>
    );
  }
}

ColumnList.propTypes = {
  addable: PropTypes.bool,
  group: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
      relationships: PropTypes.shape({
        members: PropTypes.shape({
          data: PropTypes.shape({
            directMembers: PropTypes.arrayOf(PropTypes.shape({})),
            indirectMembers: PropTypes.arrayOf(PropTypes.shape({})),
          }),
        }),
      }),
    }).isRequired,
  }),
  items: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  meta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
  }).isRequired,
  user: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }).isRequired,
    relationships: PropTypes.shape({
      groups: PropTypes.shape({
        data: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
      }),
    }),
  }),
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
};

ColumnList.defaultProps = {
  addable: false,
  group: undefined,
  user: undefined,
};

export default ColumnList;
