import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import ColumnLayout from '../../containers/ColumnLayout/ColumnLayout';
import './UserGroupsSummary.css';
import { getTitleOrPrimaryAttributeValue } from '../../utils/helpers';

const UserGroupsSummary = ({
  groups,
  groupsMeta,
  columnCount,
  columnSize,
}) => {
  // Get groups that have a title attribute to display (config error might result in empty title)
  const displayableGroups = groups
    .reduce((result, group) => {
      const groupMeta = groupsMeta[group.type].attributes;
      const titleAttribute = getTitleOrPrimaryAttributeValue(group.attributes, groupMeta);
      if (titleAttribute) {
        result.push({
          id: group.id,
          displayName: titleAttribute,
        });
      }
      return result;
    }, []);
  // Divide groups into chunks of columnSize and get rid of null entries via filter
  const groupColumns = displayableGroups
    .map((_group, i) => (i % columnSize === 0 && displayableGroups.slice(i, i + columnSize)))
    .filter(group => group);
  const remainingGroupsCount = displayableGroups.length - (columnCount * columnSize);
  const remainingGroupsMessage = (
    <FormattedMessage
      id="components.user-groups-summary.remaining-groups"
      values={{
        count: remainingGroupsCount,
      }}
    />
  );

  return (
    <div className="UserGroupsSummary">
      <div className="UserGroupsSummary__title">
        <FormattedMessage
          id="components.user-groups-summary.title"
          values={{
            count: displayableGroups.length,
          }}
        />
      </div>
      <ColumnLayout columns={Math.min(columnCount, groupColumns.length)}>
        {/* eslint-disable react/no-array-index-key */}
        {
          groupColumns.slice(0, columnCount).map((column, index) => (
            <div key={index} className="UserGroupsSummary__group-column">
              {
                column.map(group => (
                  <div key={group.id} className="UserGroupsSummary__group">
                    {group.displayName}
                  </div>
                ))
              }
            </div>
          ))
        }
      </ColumnLayout>
      {remainingGroupsCount > 0 && remainingGroupsMessage}
    </div>
  );
};

UserGroupsSummary.propTypes = {
  groups: PropTypes.arrayOf(PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }),
  })).isRequired,
  groupsMeta: PropTypes.shape({}).isRequired,
  columnCount: PropTypes.number,
  columnSize: PropTypes.number,
};

UserGroupsSummary.defaultProps = {
  columnCount: 8,
  columnSize: 10,
};

export default UserGroupsSummary;
