import React, { Component } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import FormCheckbox from 'ui-library/lib/components/forms/FormCheckbox';
import FormTextField from 'ui-library/lib/components/forms/form-text-field';
import Calendar from 'ui-library/lib/components/calendars/Calendar';

import CustomAttributeField from './CustomAttributeField/CustomAttributeField';

class EditableField extends Component {
  constructor(props) {
    super(props);

    this.handleUpdate = this.handleUpdate.bind(this);
    this.handleDateChange = this.handleDateChange.bind(this);
    this.renderField = this.renderField.bind(this);
  }

  handleUpdate(value) {
    const { attr, onEdit } = this.props;
    onEdit(attr, value);
  }

  handleDateChange(date) {
    const { attr, onEdit } = this.props;
    onEdit(attr, moment(date, 'MM-DD-YYYY'));
  }

  renderField() {
    const {
      attr,
      hasError,
      errorMessage,
      label,
      readOnly,
      type,
      required,
      customPresentation,
      value,
    } = this.props;

    // TODO: Remove false when custom attributes are ready.
    // eslint-disable-next-line no-constant-condition
    if (customPresentation && false) {
      return (
        <>
          <span className="label-text">
            {this.props.label}
          </span>
          <CustomAttributeField
            attr={attr}
            value={value}
            onUpdate={this.handleUpdate}
          />
        </>
      );
    } else if (type === 'string') {
      return (
        <div className="TextField">
          <FormTextField
            labelText={label}
            value={value}
            onValueChange={this.handleUpdate}
            readOnly={readOnly}
            errorMessage={hasError ? errorMessage : ''}
            required={required}
            flexWidth
          />
        </div>
      );
    } else if (type === 'boolean') {
      return (
        <div className="Checkbox">
          <FormCheckbox
            stateless
            className="inline"
            label={label}
            checked={value}
            onValueChange={this.handleUpdate}
            disabled={readOnly}
            errorMessage={hasError ? errorMessage : ''}
          />
        </div>
      );
    } else if (type === 'dateTime') {
      return (
        <div className="DateTime">
          <Calendar
            closeOnSelect
            date={value}
            required={required}
            label={label}
            onValueChange={this.handleDateChange}
            errorMessage={hasError ? errorMessage : ''}
          />
        </div>
      );
    }

    return null;
  }

  render() {
    return (
      <div className="EditableField">
        {this.renderField()}
      </div>
    );
  }
}

EditableField.propTypes = {
  attr: PropTypes.string.isRequired,
  hasError: PropTypes.bool.isRequired,
  errorMessage: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  onEdit: PropTypes.func.isRequired,
  readOnly: PropTypes.bool.isRequired,
  type: PropTypes.string.isRequired,
  required: PropTypes.bool,
  customPresentation: PropTypes.bool.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.bool,
    PropTypes.number,
    PropTypes.object,
    PropTypes.string,
  ]),
};

EditableField.defaultProps = {
  required: false,
  value: undefined,
};

export default EditableField;
