import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Spinner from 'ui-library/lib/components/general/Spinner';
import InlineMessage from 'ui-library/lib/components/general/InlineMessage';
import ReactIframeResizer from 'react-iframe-resizer-super';
import { FormattedMessage } from 'react-intl';

import { checkURLResponse } from '../../../../utils/helpers';

class CustomAttributeField extends Component {
  constructor(props) {
    super(props);

    // FIXME: May not work if app URL contains multiple hash characters
    [this.origin] = window.location.href.split('#');
    this.path = `${process.env.PUBLIC_URL}/customAttributes/${props.attr}.html`;

    this.checkForTemplate = this.checkForTemplate.bind(this);
    this.sendMessage = this.sendMessage.bind(this);
    this.receiveMessage = this.receiveMessage.bind(this);

    this.state = {
      isLoading: true,
      templateExists: undefined,
      isInitialized: false,
    };
  }

  componentDidMount() {
    this.checkForTemplate(this.path);
  }

  componentDidUpdate() {
    if (this.state.templateExists) {
      window.addEventListener('message', this.receiveMessage);
    }
  }

  componentWillUnmount() {
    if (this.state.templateExists) {
      window.removeEventListener('message', this.receiveMessage);
    }
  }

  async checkForTemplate(path) {
    const pathExists = await checkURLResponse(path);
    this.setState({ isLoading: false, templateExists: !!pathExists });
  }

  sendMessage(message) {
    const iframe = document.getElementById(this.props.attr);
    iframe.contentWindow.postMessage(message, this.origin);
  }

  receiveMessage(message) {
    const messageData = message && message.data;
    if (typeof messageData === 'undefined') return;

    const { attr, value } = this.props;
    const { action, data, attribute } = messageData;

    if (!this.state.isInitialized) {
      if (action === 'iframeLoaded') {
        this.sendMessage({
          action: 'setAttribute',
          data: attr,
          origin: this.origin,
        });
      }
    }

    if (attribute === attr) {
      if (action === 'iframeInitialized') {
        this.setState({
          isInitialized: true,
        }, () => {
          this.sendMessage({
            action: 'setValue',
            data: value,
          });
        });
      } else if (action === 'setValue') {
        this.props.onUpdate(data);
      }
    }
  }

  render() {
    const { attr } = this.props;
    const { isLoading, templateExists } = this.state;

    if (isLoading) return <Spinner show />;

    if (templateExists) {
      return (
        <ReactIframeResizer
          src={this.path}
          id={attr}
        />
      );
    }

    return (
      <>
        <InlineMessage type={InlineMessage.MessageTypes.ERROR}>
          <FormattedMessage id="components.editable-field.missing-template" />
        </InlineMessage>
      </>
    );
  }
}

CustomAttributeField.propTypes = {
  attr: PropTypes.string.isRequired,
  onUpdate: PropTypes.func.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.bool,
    PropTypes.number,
    PropTypes.object,
    PropTypes.string,
  ]),
};

CustomAttributeField.defaultProps = {
  value: undefined,
};

export default CustomAttributeField;
