import React, { Fragment } from 'react';
import { Route, HashRouter as Router, Switch, Redirect } from 'react-router-dom';
import { Provider } from 'react-redux';
import { IntlProvider } from 'react-intl-redux';
import { OidcProvider } from 'redux-oidc';

import AppTimer from './AppTimer';
import NavFrame from './nav/NavFrame';
import CallbackPage from './auth/CallbackPage';
import SignOnPage from './auth/SignOnPage';
import Signout from './auth/Signout';
import SearchPage from './search/SearchPage';
import RequireAuth from './hoc/RequireAuth';
import NoRequireAuth from './hoc/NoRequireAuth';
import NewUserPage from './user/NewUserPage/NewUserPage';
import UserProfilePage from './user/UserProfilePage';
import NewGroupPage from './group/NewGroupPage/NewGroupPage';
import GroupProfilePage from './group/GroupProfilePage';
import NotFoundPage from './NotFoundPage';
import MessageContainer from './nav/MessageContainer';
import ErrorBoundary from './error/ErrorBoundary';
import store from '../store';
import userManager from '../utils/userManager';
import * as routes from '../utils/routes';

import './App.css';

const AuthNavFrame = RequireAuth(NavFrame, false);
const AuthMessageContainer = RequireAuth(MessageContainer, false);

const App = () => (
  <ErrorBoundary>
    <Provider store={store}>
      <OidcProvider store={store} userManager={userManager}>
        <IntlProvider locale="en" textComponent={React.Fragment}>
          <Router>
            <Fragment>
              <AuthNavFrame />
              <div className="App">
                <AppTimer />
                <AuthMessageContainer />
                <Switch>
                  <Route exact path={routes.BASE_ROUTE} component={NoRequireAuth(SignOnPage)} />
                  <Route exact path={routes.CALLBACK_ROUTE} component={CallbackPage} />
                  <Route
                    exact
                    path={routes.SIGNOUT_ROUTE}
                    component={RequireAuth(Signout, true, true)}
                  />
                  <Route exact path="/search/:id(users|groups)" component={RequireAuth(SearchPage)} />
                  <Route exact path={routes.USERS_NEW_ROUTE} component={RequireAuth(NewUserPage)} />
                  <Route exact path="/users/:id" component={RequireAuth(UserProfilePage)} />
                  <Route
                    exact
                    path={routes.GROUPS_NEW_ROUTE}
                    component={RequireAuth(NewGroupPage)}
                  />
                  <Route exact path="/groups/:id" component={RequireAuth(GroupProfilePage)} />
                  <Route path={routes.NOT_FOUND_ROUTE} component={RequireAuth(NotFoundPage)} />
                  <Redirect to={routes.NOT_FOUND_ROUTE} />
                </Switch>
              </div>
            </Fragment>
          </Router>
        </IntlProvider>
      </OidcProvider>
    </Provider>
  </ErrorBoundary>
);

export default App;
