import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import _ from 'lodash';
import InlineMessage, { MessageTypes } from 'ui-library/lib/components/general/InlineMessage';

import ColumnList from './ColumnList';
import { MODEL_TYPES, PROP_MODEL_TYPES } from '../../../utils/modelTypes';

class Column extends Component {
  constructor(props) {
    super(props);

    this.displayUserProfileColumn = this.displayUserProfileColumn.bind(this);
    this.displayGroupProfileColumn = this.displayGroupProfileColumn.bind(this);
  }

  displayUserProfileColumn(options = {}) {
    const {
      user,
      userGroups,
      userMeta,
      included,
      profilePageModel,
      queryString,
      userName,
    } = this.props;
    const hasGroups = _.get(included, 'length', 0);
    const addable = _.get(options, 'addable', false);

    if (
      addable &&
      _.get(userGroups, 'addableGroups.length', 0)
    ) {
      return (
        <ColumnList
          addable={addable}
          items={userGroups.addableGroups}
          meta={userMeta}
          user={user}
          profilePageModel={profilePageModel}
        />
      );
    } else if (
      !addable &&
      _.get(userGroups, 'currentGroups.length', 0)
    ) {
      return (
        <ColumnList
          addable={addable}
          items={userGroups.currentGroups}
          meta={userMeta}
          user={user}
          profilePageModel={profilePageModel}
        />
      );
    } else if (
      !addable &&
      hasGroups &&
      !queryString.length
    ) {
      return (
        <ColumnList
          addable={addable}
          items={included}
          meta={userMeta}
          user={user}
          profilePageModel={profilePageModel}
        />
      );
    } else if (addable && !queryString.length) {
      return (
        <InlineMessage
          className="column-message"
          type={MessageTypes.NOTICE}
        >
          <FormattedMessage
            id="components.dual-column-search.users.column.enter-search-prompt"
          />
        </InlineMessage>
      );
    } else if (queryString.length) {
      return (
        <InlineMessage
          className="column-message"
          type={MessageTypes.WARNING}
        >
          <FormattedMessage
            id="components.dual-column-search.users.column.empty-search"
            values={{
              queryString,
            }}
          />
        </InlineMessage>
      );
    }

    return (
      <InlineMessage
        className="column-message"
        type={MessageTypes.NOTICE}
      >
        <FormattedMessage
          id="components.dual-column-search.users.column.empty-list"
          values={{
            userName,
          }}
        />
      </InlineMessage>
    );
  }

  displayGroupProfileColumn(options = {}) {
    const {
      group,
      groupMeta,
      groupMembers,
      included,
      profilePageModel,
      queryString,
      groupTitle,
    } = this.props;
    const hasIncluded = _.get(included, 'length', 0);
    const addable = _.get(options, 'addable', false);
    const nonMembersExceeded = addable &&
      _.get(groupMembers, 'meta.nonMembers.sizeLimitExceeded', false);
    const membersExceeded = !addable && (
      _.get(groupMembers, 'meta.directMembers.sizeLimitExceeded', false) ||
      _.get(groupMembers, 'meta.indirectMembers.sizeLimitExceeded', false)
    );
    const groupsError = addable && _.get(groupMembers, 'groupsError', false);

    if (
      addable &&
      _.get(groupMembers, 'nonMembers.length', 0)
    ) {
      return (
        <>
          {
            nonMembersExceeded &&
            <InlineMessage
              className="column-message"
              type={MessageTypes.WARNING}
            >
              <FormattedMessage
                id="components.dual-column-search.groups.column.users-exceeded"
                values={{
                  queryString,
                }}
              />
            </InlineMessage>
          }
          {
            groupsError &&
            <InlineMessage
              className="column-message"
              type={MessageTypes.WARNING}
            >
              <FormattedMessage
                id="components.dual-column-search.groups.column.groups-exceeded"
                values={{
                  queryString,
                }}
              />
            </InlineMessage>
          }
          <ColumnList
            addable={addable}
            items={groupMembers.nonMembers}
            meta={groupMeta}
            group={group}
            profilePageModel={profilePageModel}
          />
        </>
      );
    } else if (
      !addable &&
      _.get(groupMembers, 'members.length', 0)
    ) {
      return (
        <ColumnList
          addable={addable}
          items={groupMembers.members}
          meta={groupMeta}
          group={group}
          profilePageModel={profilePageModel}
        />
      );
    } else if (
      !addable &&
      !groupMembers &&
      hasIncluded
    ) {
      return (
        <ColumnList
          addable={addable}
          items={included}
          meta={groupMeta}
          group={group}
          profilePageModel={profilePageModel}
        />
      );
    } else if (nonMembersExceeded) {
      return (
        <InlineMessage
          className="column-message"
          type={MessageTypes.WARNING}
        >
          <FormattedMessage
            id="components.dual-column-search.groups.column.size-limit-exceeded"
            values={{
              queryString,
            }}
          />
        </InlineMessage>
      );
    } else if (membersExceeded) {
      return (
        <InlineMessage
          className="column-message"
          type={MessageTypes.WARNING}
        >
          <FormattedMessage
            id="components.dual-column-search.groups.column.result-too-large"
          />
        </InlineMessage>
      );
    } else if (addable && !queryString.length) {
      return (
        <InlineMessage
          className="column-message"
          type={MessageTypes.NOTICE}
        >
          <FormattedMessage
            id="components.dual-column-search.groups.column.enter-search-prompt"
          />
        </InlineMessage>
      );
    } else if (queryString.length) {
      return (
        <InlineMessage
          className="column-message"
          type={MessageTypes.WARNING}
        >
          <FormattedMessage
            id="components.dual-column-search.groups.column.empty-search"
            values={{
              queryString,
            }}
          />
        </InlineMessage>
      );
    }

    return (
      <InlineMessage
        className="column-message"
        type={MessageTypes.NOTICE}
      >
        <FormattedMessage
          id="components.dual-column-search.groups.column.empty-list"
          values={{
            groupTitle,
          }}
        />
      </InlineMessage>
    );
  }

  render() {
    const { addable, profilePageModel } = this.props;
    const columnNumber = addable ? 1 : 2;

    return (
      <div className="Column">
        <div className="column-header">
          <span className="column-title">
            <FormattedMessage
              id={`components.dual-column-search.${profilePageModel}.column.title.column-${columnNumber}`}
            />
          </span>
        </div>
        {
          profilePageModel === MODEL_TYPES.USERS ?
          this.displayUserProfileColumn({ addable }) :
          this.displayGroupProfileColumn({ addable })
        }
      </div>
    );
  }
}

Column.propTypes = {
  addable: PropTypes.bool,
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  group: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
      relationships: PropTypes.shape({
        members: PropTypes.shape({
          data: PropTypes.shape({
            directMembers: PropTypes.arrayOf(PropTypes.shape({})),
            indirectMembers: PropTypes.arrayOf(PropTypes.shape({})),
          }),
        }),
      }),
    }).isRequired,
  }),
  groupMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  groupMembers: PropTypes.shape({
    groupId: PropTypes.string.isRequired,
    queryString: PropTypes.string.isRequired,
    members: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    nonMembers: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    meta: PropTypes.shape({
      directMembers: PropTypes.shape({}),
      indirectMembers: PropTypes.shape({}),
      nonMembers: PropTypes.shape({}),
    }).isRequired,
    error: PropTypes.string,
    groupsError: PropTypes.bool,
  }),
  included: PropTypes.arrayOf(PropTypes.shape({})),
  queryString: PropTypes.string,
  userGroups: PropTypes.shape({
    queryString: PropTypes.string.isRequired,
    addableGroups: PropTypes.arrayOf(PropTypes.shape({})),
    currentGroups: PropTypes.arrayOf(PropTypes.shape({})),
    error: PropTypes.string,
  }),
  userMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  user: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }).isRequired,
    relationships: PropTypes.shape({
      groups: PropTypes.shape({
        data: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
      }),
    }),
  }),
  userName: PropTypes.string,
  groupTitle: PropTypes.string,
};

Column.defaultProps = {
  addable: false,
  group: undefined,
  groupMeta: undefined,
  groupMembers: undefined,
  included: undefined,
  queryString: '',
  userGroups: undefined,
  userMeta: undefined,
  user: undefined,
  userName: '',
  groupTitle: '',
};

export default Column;
