import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import _ from 'lodash';

import Permission from '../../../containers/Permission/Permission';
import NewModelPage from '../../../containers/NewModelPage/NewModelPage';
import { createUserAction } from '../../../store/actions/userActions';
import { MODEL_TYPES } from '../../../utils/modelTypes';
import { RESOURCE_TYPES } from '../../../utils/resourceTypes';
import { PERMISSIONS } from '../../../utils/permissions/permissions';

export const NewUserPage = ({
  createAction,
  errorAttrs,
}) => {
  const resourceType = RESOURCE_TYPES.USER;
  return (
    <Permission
      requiredPermissions={[PERMISSIONS.CREATE]}
      resourceType={resourceType}
    >
      <NewModelPage
        title={
          <FormattedMessage
            id="page.new-model.page-header"
            values={{
              resourceType: _.capitalize(resourceType),
            }}
          />
        }
        modelType={MODEL_TYPES.USERS}
        resourceType={resourceType}
        createAction={createAction}
        errorAttrs={errorAttrs}
      />
    </Permission>
  );
};

NewUserPage.propTypes = {
  createAction: PropTypes.func.isRequired,
  errorAttrs: PropTypes.arrayOf(PropTypes.string),
};

NewUserPage.defaultProps = {
  errorAttrs: undefined,
};

function mapStateToProps(state) {
  const { errorAttrs } = state.user;

  return {
    errorAttrs,
  };
}

export default connect(
  mapStateToProps,
  {
    createAction: createUserAction,
  },
)(NewUserPage);
