import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage, intlShape, injectIntl } from 'react-intl';
import ConfirmTooltip from 'ui-library/lib/components/tooltips/ConfirmTooltip';

export class ResetPasswordButton extends Component {
  constructor(props) {
    super(props);

    this.handleToggle = this.handleToggle.bind(this);
    this.handleConfirm = this.handleConfirm.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
    this.resetButton = this.resetButton.bind(this);

    this.state = {
      confirmIsOpen: false,
    };
  }

  handleToggle() {
    this.toggleModal({ confirmIsOpen: true });
  }

  handleConfirm() {
    const { resetUserPassword } = this.props;
    this.toggleModal({ confirmIsOpen: false, callback: resetUserPassword });
  }

  handleCancel() {
    this.toggleModal({ confirmIsOpen: false });
  }

  toggleModal(options = {}) {
    const { disabled } = this.props;
    const { confirmIsOpen, callback } = options;

    if (disabled) return;
    if (typeof confirmIsOpen !== 'undefined') {
      this.setState({
        confirmIsOpen,
      }, () => typeof callback === 'function' && callback());
    } else {
      this.setState({
        confirmIsOpen: !this.state.confirmIsOpen,
      }, () => typeof callback === 'function' && callback());
    }
  }

  resetButton() {
    const { disabled } = this.props;
    return (
      <button className="inline" disabled={disabled}>
        <FormattedMessage id="components.admin-actions.password-reset-button.reset-password" />
      </button>
    );
  }

  render() {
    const { intl, disabled } = this.props;

    return (
      <div className="ResetPasswordButton">
        <ConfirmTooltip
          title={intl.formatMessage({
            id: 'components.admin-actions.password-reset-button.reset-password',
          })}
          label={this.resetButton()}
          onToggle={this.handleToggle}
          open={this.state.confirmIsOpen}
          onConfirm={this.handleConfirm}
          onCancel={this.handleCancel}
          disableSave={disabled}
          positionClassName="bottom left"
          onClose={this.handleCancel}
        >
          <FormattedMessage id="components.admin-actions.password-reset-button.modal-content" />
        </ConfirmTooltip>
      </div>
    );
  }
}

ResetPasswordButton.propTypes = {
  intl: intlShape.isRequired,
  disabled: PropTypes.bool,
  resetUserPassword: PropTypes.func.isRequired,
};

ResetPasswordButton.defaultProps = {
  disabled: false,
};

export default injectIntl(ResetPasswordButton);
