import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import Icon from 'ui-library/lib/components/general/Icon';
import HelpHint from 'ui-library/lib/components/tooltips/HelpHint';

import ModifyMemberButton from './ModifyMemberButton';
import ModelSummary from '../../../containers/ModelSummary/ModelSummary';
import { PROP_MODEL_TYPES } from '../../../utils/modelTypes';
import { getResourceById, iconFor } from '../../../utils/resourceTypes';
import { blacklistedDisplayAttrs } from '../../../utils/helpers';

import './ColumnListItem.css';

class ColumnListItem extends Component {
  constructor(props) {
    super(props);

    this.itemContent = this.itemContent.bind(this);
    this.tooltipContent = this.tooltipContent.bind(this);
    this.displayTooltip = this.displayTooltip.bind(this);
  }

  tooltipContent() {
    const {
      item,
      meta,
      group,
      user,
    } = this.props;
    const includedMeta =
      item.type === _.get(group, 'data.type') || item.type === _.get(user, 'data.type')
        ? meta
        : meta.included[item.type].attributes;

    return (
      <ModelSummary
        attributes={item.attributes}
        id={item.id}
        meta={includedMeta}
      />
    );
  }

  itemContent() {
    const {
      item,
      itemName,
      addable,
      user,
      group,
      profilePageModel,
    } = this.props;
    const { resourceType } = getResourceById(item.type).attributes;
    const iconName = iconFor(resourceType);

    return (
      <div className="item-row">
        <div className="item-row__labels">
          <div className="item-row__name">
            <Icon iconName={iconName} />
            {itemName}
          </div>
        </div>
        <ModifyMemberButton
          addMember={addable}
          user={(user && user.data) || item}
          group={(group && group.data) || item}
          profilePageModel={profilePageModel}
        />
      </div>
    );
  }

  displayTooltip() {
    return (
      <HelpHint
        placement="top"
        hintText={this.tooltipContent()}
        delayShow={400}
      >
        {this.itemContent()}
      </HelpHint>
    );
  }

  render() {
    const { item } = this.props;
    const displayAttrs =
      Object
        .keys(item.attributes)
        .filter(attr => blacklistedDisplayAttrs.indexOf(attr) < 0);

    return (
      <Fragment>
        <li
          key={item.id}
          className="ColumnListItem"
          onMouseEnter={this.debouncedOpen}
          onMouseLeave={this.handleClose}
        >
          {
            displayAttrs.length > 1
              ? this.displayTooltip()
              : this.itemContent()
          }
        </li>
      </Fragment>
    );
  }
}

ColumnListItem.propTypes = {
  addable: PropTypes.bool,
  group: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
      relationships: PropTypes.shape({
        members: PropTypes.shape({
          data: PropTypes.shape({
            directMembers: PropTypes.arrayOf(PropTypes.shape({})),
            indirectMembers: PropTypes.arrayOf(PropTypes.shape({})),
          }),
        }),
      }),
    }).isRequired,
  }),
  item: PropTypes.shape({}).isRequired,
  itemName: PropTypes.string.isRequired,
  user: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }).isRequired,
    relationships: PropTypes.shape({
      groups: PropTypes.shape({
        data: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
      }),
    }),
  }),
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  meta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
  }).isRequired,
};

ColumnListItem.defaultProps = {
  addable: false,
  group: undefined,
  user: undefined,
};

export default ColumnListItem;
