import React from 'react';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import _ from 'lodash';
import permissions from '../../utils/permissions/permissions';
import { NOT_FOUND_ROUTE } from '../../utils/routes';
import { PROP_RESOURCE_TYPES } from '../../utils/resourceTypes';

const Permission = ({
  children,
  requiredPermissions,
  resourceId,
  resourceType,
  redirect,
}) => {
  let canRender = false;

  if (resourceId) {
    canRender =
      _.difference(requiredPermissions, permissions.permissionsFor(resourceId))
        .length === 0;
  } else if (resourceType) {
    canRender =
      requiredPermissions.every(permission => permissions
        .hasPermissionForType(permission, resourceType));
  }

  if (!canRender && redirect) return <Redirect to={NOT_FOUND_ROUTE} />;
  if (!canRender) return null;

  return (
    <>
      {children}
    </>
  );
};

Permission.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
  requiredPermissions: PropTypes.arrayOf(PropTypes.string).isRequired,
  resourceId: PropTypes.string,
  resourceType: PropTypes.oneOf(PROP_RESOURCE_TYPES),
  redirect: PropTypes.bool,
};

Permission.defaultProps = {
  redirect: true,
  resourceId: undefined,
  resourceType: undefined,
};

export default Permission;
