import React from 'react';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import LabelValuePairs from 'ui-library/lib/components/layout/LabelValuePairs';
import AttributeCategoryList from './AttributeCategoryList/AttributeCategoryList';
import { blacklistedDisplayAttrs, categorizeAttributes } from '../../utils/helpers';

export const ModelSummary = ({
  intl,
  attributes,
  blacklist,
  meta,
}) => {
  function renderAttributes() {
    const filteredAttributes =
      Object
        .entries(meta.attributes)
        .filter(([key, _value]) =>
          Object.keys(attributes).indexOf(key) >= 0 && blacklist.indexOf(key) < 0);
    const categories = meta.attributeCategories;
    const attributePairings = [];

    if (categories && categories.length) {
      const categoryObjects = categorizeAttributes(categories, filteredAttributes, intl);
      return (
        <AttributeCategoryList
          categoryObjects={categoryObjects}
          attributes={attributes}
        />
      );
    }

    filteredAttributes.forEach(([key, _value]) => {
      attributePairings.push({
        label: meta.attributes[key].displayName,
        value: attributes[key].join(', '),
      });
    });

    return <LabelValuePairs dataPairs={attributePairings} />;
  }

  return (
    <div className="ModelSummary">
      {renderAttributes()}
    </div>
  );
};

ModelSummary.propTypes = {
  intl: intlShape.isRequired,
  attributes: PropTypes.shape({
    'dadmin-account-locked': PropTypes.arrayOf(PropTypes.bool),
    'ds-pwp-account-disabled': PropTypes.arrayOf(PropTypes.bool),
  }).isRequired,
  blacklist: PropTypes.arrayOf(PropTypes.string),
  meta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
  }).isRequired,
};

ModelSummary.defaultProps = {
  blacklist: blacklistedDisplayAttrs,
};

export default injectIntl(ModelSummary);
