import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage, intlShape, injectIntl } from 'react-intl';
import { Link, Redirect } from 'react-router-dom';
import _ from 'lodash';
import Spinner from 'ui-library/lib/components/general/Spinner';
import RockerButton from 'ui-library/lib/components/forms/RockerButton';
import LeftNavBarActions from 'ui-library/lib/components/panels/left-nav/Actions';

import Permission from '../../containers/Permission/Permission';
import AdminActions from '../shared/admin-actions/AdminActions';
import GeneratedPasswordModal from '../shared/GeneratedPasswordModal';
import AlertModal from '../shared/AlertModal';
import ModelForm from '../../containers/ModelForm/ModelForm';
import DualColumnSearch from '../shared/dual-column-search/DualColumnSearch';
import {
  getUserAction,
  editUserAction,
  clearNewUserAction,
} from '../../store/actions/userActions';
import { filterResourceTypesAction } from '../../store/actions/resourceTypes/resourceTypes';
import { getSearchResults } from '../../store/actions/search/search';
import { HTTP_ERRORS, displayPrimaryAttr } from '../../utils/helpers';
import permissions, { PERMISSIONS } from '../../utils/permissions/permissions';
import { MODEL_TYPES } from '../../utils/modelTypes';
import { NOT_FOUND_ROUTE } from '../../utils/routes';
import './UserProfilePage.css';
import { RESOURCE_TYPES } from '../../utils/resourceTypes';

export class UserProfilePage extends Component {
  constructor(props) {
    super(props);

    const isNewUser = !!this.props.newUser;
    const newPassword = _.get(this.props, 'newUser.data.attributes.dadmin-generated-password[0]', undefined);

    this.labels = [
      props.intl.formatMessage({ id: 'components.rocker-button.profile' }),
      props.intl.formatMessage({ id: 'components.rocker-button.groups' }),
    ];

    this.handleUserEdit = this.handleUserEdit.bind(this);
    this.searchQuery = this.searchQuery.bind(this);
    this.clearSearch = this.clearSearch.bind(this);
    this.handleButtonChange = this.handleButtonChange.bind(this);
    this.displayContent = this.displayContent.bind(this);
    this.groupsContent = this.groupsContent.bind(this);
    this.profileContent = this.profileContent.bind(this);
    this.toggleModal = this.toggleModal.bind(this);

    this.state = {
      userName: '',
      isSaving: false,
      isLoading: true,
      isLoadingContent: false,
      selectedIndex: 0,
      newPassword,
      renderNewPasswordModal: isNewUser && !_.isUndefined(newPassword),
      renderAlertModal: isNewUser && _.isUndefined(newPassword),
    };
  }

  componentDidMount() {
    this.getUser();
  }

  componentDidUpdate(prevProps, _prevState) {
    // NOTE: We are ok to use setState here because of the conditional
    // https://reactjs.org/docs/react-component.html#componentdidupdate
    /* eslint-disable react/no-did-update-set-state */
    const {
      user,
      userGroups,
      primarySearchAttribute,
      meta,
      newUser,
    } = this.props;
    const { newPassword } = this.state;

    if (user !== prevProps.user) {
      const isNewUser = !!newUser;
      this.setState({
        isLoading: false,
        renderNewPasswordModal: isNewUser && !_.isUndefined(newPassword),
      });
    } else if (userGroups !== prevProps.userGroups) {
      this.setState({ isLoadingContent: false });
    } else if (primarySearchAttribute && !this.state.userName.length) {
      this.setState({
        userName: displayPrimaryAttr(user.data.attributes, primarySearchAttribute, meta),
      });
    }
  }

  async getUser() {
    const { id, type } = this.props.location.state;
    await this.props.getUserAction(id, type);
    this.setState({ isLoading: false });
  }

  filterResourceTypes() {
    const filter = {
      byType: [RESOURCE_TYPES.GROUP],
      byPermission: PERMISSIONS.MANAGE_GROUP_MEMBERSHIP,
      primary: false,
    };
    this.props.filterResourceTypesAction(filter);
  }

  handleButtonChange(e) {
    const selectedIndex = _.get(e, 'index', 0);

    if (selectedIndex === 1) {
      this.filterResourceTypes();
      this.setState({ selectedIndex }, () => this.searchQuery('', { skipMessage: true }));
    } else {
      this.setState({ selectedIndex });
    }
  }

  async handleUserEdit(updatedAttrs) {
    const { id } = this.props.user.data;
    this.setState({ isSaving: true });

    try {
      const result = await this.props.editUserAction(id, updatedAttrs);
      this.setState({ isSaving: false });
      return Promise.resolve(result);
    } catch (error) {
      this.setState({ isSaving: false });
      throw error;
    }
  }

  searchQuery(queryString = '', options = {}) {
    const isLoadingContent = !('hideSpinner' in options && options.hideSpinner);
    this.setState({ isLoadingContent }, () => {
      this.props.getSearchResults(queryString, {
        ...options,
        subquery: true,
        profilePageModel: MODEL_TYPES.USERS,
      });
    });
  }

  clearSearch() {
    this.searchQuery('', { skipMessage: true, hideSpinner: true });
  }

  profileContent() {
    const {
      error,
      user,
      meta,
      errorAttrs,
    } = this.props;
    const { isSaving } = this.state;

    return (
      <div className="profile-content">
        <ModelForm
          model={user}
          error={error}
          meta={meta}
          isSaving={isSaving}
          onSave={this.handleUserEdit}
          errorAttrs={errorAttrs}
          selectItemLeftNavAction={this.props.selectItemLeftNavAction}
        />
      </div>
    );
  }

  groupsContent() {
    const {
      user,
      meta,
      userGroups,
      included,
    } = this.props;
    const { userName, isLoadingContent } = this.state;

    return (
      <div className="groups-content">
        <DualColumnSearch
          profilePageModel={MODEL_TYPES.USERS}
          user={user}
          userGroups={userGroups}
          queryString={userGroups && userGroups.queryString}
          userMeta={meta}
          clearSearch={this.clearSearch}
          searchQuery={this.searchQuery}
          included={included}
          userName={userName}
          isLoadingSearch={isLoadingContent}
          dropDownProps={{
            showDropDown: true,
            dropDownFilter: [RESOURCE_TYPES.GROUP],
            primaryDropDown: false,
          }}
        />
      </div>
    );
  }

  displayContent() {
    const { groupsPermission } = this.props;
    const { selectedIndex } = this.state;

    if (selectedIndex === 0) {
      return this.profileContent();
    } else if (selectedIndex === 1 && groupsPermission) {
      return this.groupsContent();
    }

    return null;
  }

  toggleModal() {
    this.setState({ renderNewPasswordModal: false, renderAlertModal: false }, () => {
      this.props.clearNewUserAction();
    });
  }

  render() {
    const { intl, error, user } = this.props;
    const {
      userName,
      isLoading,
      newPassword,
      renderNewPasswordModal,
      renderAlertModal,
    } = this.state;
    if (error === HTTP_ERRORS[404]) return <Redirect to={NOT_FOUND_ROUTE} />;
    if (!user || isLoading) return <Spinner show />;

    return (
      <Permission
        requiredPermissions={[PERMISSIONS.UPDATE]}
        resourceId={user.data.type}
      >
        <div className="UserProfilePage">
          <div className="breadcrumbs">
            <Link to="/search/users">
              <span className="inline-icon icon-dropdown-arrow" />
              <FormattedMessage id="breadcrumbs.page.search" />
            </Link>
          </div>

          {
            renderNewPasswordModal &&
            <GeneratedPasswordModal
              isOpen={renderNewPasswordModal}
              modalTitle={intl.formatMessage({ id: 'components.generated-password-modal.user-creation.title' })}
              modalDescription={<FormattedMessage id="components.generated-password-modal.description" />}
              toggleModal={this.toggleModal}
              password={newPassword}
            />
          }

          <AlertModal
            isOpen={renderAlertModal}
            modalTitle={intl.formatMessage({ id: 'page.user-profile.alert-modal.title' })}
            modalDescription={intl.formatMessage({ id: 'page.user-profile.alert-modal.message' })}
            onSave={this.toggleModal}
            saveText={intl.formatMessage({ id: 'common.close' })}
          />

          <div className="page-header">
            <div className="text-page-title header-child">
              {userName}
            </div>

            {
              <div className="admin-actions header-child">
                <AdminActions
                  userId={user.data.id}
                  isLocked={user.data.attributes['dadmin-account-locked'] ? false : user.data.attributes['dadmin-account-locked'][0]}
                  isDisabled={_.isUndefined(user.data.attributes['ds-pwp-account-disabled']) ? false : user.data.attributes['ds-pwp-account-disabled'][0]}
                />
              </div>
            }
          </div>

          {
            this.props.groupsPermission &&
            <div className="profile-switch">
              <RockerButton
                stateless
                selectedIndex={this.state.selectedIndex}
                onValueChange={this.handleButtonChange}
                labels={this.labels}
              />
            </div>
          }

          <div className="page-body">
            {this.displayContent()}
          </div>
        </div>
      </Permission>
    );
  }
}

UserProfilePage.propTypes = {
  intl: intlShape.isRequired,
  editUserAction: PropTypes.func.isRequired,
  getUserAction: PropTypes.func.isRequired,
  clearNewUserAction: PropTypes.func.isRequired,
  location: PropTypes.shape({
    state: PropTypes.shape({
      id: PropTypes.string.isRequired,
      type: PropTypes.string.isRequired,
    }).isRequired,
  }).isRequired,
  user: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      type: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }).isRequired,
  }),
  error: PropTypes.string,
  meta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
  }),
  primarySearchAttribute: PropTypes.string,
  errorAttrs: PropTypes.arrayOf(PropTypes.string),
  getSearchResults: PropTypes.func.isRequired,
  userGroups: PropTypes.shape({
    dataType: PropTypes.string,
    queryString: PropTypes.string.isRequired,
    results: PropTypes.arrayOf(PropTypes.shape({})),
    included: PropTypes.arrayOf(PropTypes.shape({})),
    meta: PropTypes.shape({}),
    primarySearchAttribute: PropTypes.string,
    groupTitleAttribute: PropTypes.string,
    error: PropTypes.string,
  }),
  included: PropTypes.arrayOf(PropTypes.shape({})),
  groupsPermission: PropTypes.bool.isRequired,
  newUser: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({
        'dadmin-generated-password': PropTypes.arrayOf(PropTypes.string),
      }).isRequired,
    }).isRequired,
  }),
  selectItemLeftNavAction: PropTypes.func.isRequired,
  filterResourceTypesAction: PropTypes.func.isRequired,
};

UserProfilePage.defaultProps = {
  user: undefined,
  error: undefined,
  meta: undefined,
  primarySearchAttribute: undefined,
  errorAttrs: undefined,
  userGroups: undefined,
  included: undefined,
  newUser: undefined,
};

function mapStateToProps(state) {
  const {
    user,
    included,
    errorAttrs,
    userGroups,
    error,
    meta,
    primarySearchAttribute,
  } = state.user;

  return {
    error,
    meta,
    primarySearchAttribute,
    userGroups,
    user,
    included,
    errorAttrs,
    groupsPermission: permissions.manageGroups(),
    newUser: state.user.newUser,
  };
}

export default injectIntl(connect(
  mapStateToProps,
  {
    getUserAction,
    editUserAction,
    getSearchResults,
    clearNewUserAction,
    selectItemLeftNavAction: LeftNavBarActions.selectItem,
    filterResourceTypesAction,
  },
)(UserProfilePage));
