import _ from 'lodash';
import axios from '../../../utils/axiosInstance';
import endpoints from '../../../utils/apiEndpoints';
import NullUserError from '../../../errors/NullUserError';
import OidcConnectionError from '../../../errors/OidcConnectionError';
import { generateHttpErrorMessage, logErrorToConsole } from '../../../utils/helpers';
import * as types from '../actionTypes';
import { createAction, FAILURE, REQUEST, SUCCESS } from '../index';
import ApplicationAccessError from '../../../errors/ApplicationAccessError';

const { PF_HOST, PF_PORT, DADMIN_CLIENT_ID } = window;

export const authenticationActions = {
  request: () => createAction(types.AUTHENTICATION[REQUEST]),
  success: data => createAction(types.AUTHENTICATION[SUCCESS], { data }),
  failure: error => createAction(types.AUTHENTICATION[FAILURE], { error }),
  unauthenticate: () => createAction(types.UNAUTHENTICATE_USER),
};

function getOidcTokenInfo() {
  const oidc = sessionStorage.getItem(`oidc.user:https://${PF_HOST}:${PF_PORT}:${DADMIN_CLIENT_ID}`);

  try {
    return oidc;
  } catch (error) {
    logErrorToConsole(error);
    return null;
  }
}

export function getAuthenticatedUserAction(oidc = getOidcTokenInfo()) {
  if (oidc === null) {
    return async (dispatch) => {
      dispatch(authenticationActions.failure('missing_oidc_token'));
    };
  }

  return async (dispatch) => {
    try {
      const authEndpoint = endpoints.authenticatedUser();
      if (_.isUndefined(authEndpoint)) throw new ApplicationAccessError();

      dispatch(authenticationActions.request());
      const response = await axios.get(authEndpoint);
      if (response.data.data === null) throw new NullUserError();
      dispatch(authenticationActions.success(response.data));

      return Promise.resolve(response.data);
    } catch (error) {
      logErrorToConsole(error);
      const { custom } = error;
      let intlKey;

      if (custom) {
        ({ intlKey } = error);
      } else {
        const errorMessage = generateHttpErrorMessage(error);
        ({ intlKey } = errorMessage);
      }

      dispatch(authenticationActions.failure(intlKey));

      return Promise.reject(error);
    }
  };
}

export function unauthenticateUser() {
  return async (dispatch) => {
    dispatch(authenticationActions.unauthenticate());
  };
}

export function oidcConnectionFailure(error) {
  logErrorToConsole(error);

  return async (dispatch) => {
    dispatch(authenticationActions.failure(new OidcConnectionError().intlKey));
  };
}
