import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage, intlShape, injectIntl } from 'react-intl';

import { editUserAction, resetUserPasswordAction } from '../../../store/actions/userActions';
import GeneratedPasswordModal from '../GeneratedPasswordModal';
import ResetPasswordButton from './ResetPasswordButton';
import EnableUserToggle from './EnableUserToggle';
import './AdminActions.css';

function modalDescription() {
  return (
    <>
      <FormattedMessage
        id="components.generated-password-modal.reset.description"
      />
      <br />
      <FormattedMessage
        id="components.generated-password-modal.description"
      />
    </>
  );
}

export class AdminActions extends Component {
  constructor(props) {
    super(props);

    this.toggleModal = this.toggleModal.bind(this);
    this.resetUserPassword = this.resetUserPassword.bind(this);

    this.state = {
      passwordResult: null,
      modalOpen: false,
    };
  }

  toggleModal() {
    this.setState({ modalOpen: !this.state.modalOpen });
  }

  async resetUserPassword() {
    const { userId } = this.props;
    try {
      const result = await this.props.resetUserPasswordAction(userId);

      this.setState({
        passwordResult: result,
        modalOpen: true,
      });
    } catch (error) {
      this.setState({
        passwordResult: null,
        modalOpen: false,
      });
    }
  }

  render() {
    const {
      intl,
      userId,
      isLocked,
      isDisabled,
      showResetPasswordButton,
      showEnableUserToggle,
    } = this.props;
    const { passwordResult, modalOpen } = this.state;

    return (
      <div className="AdminActions">
        {
          modalOpen &&
          <div className="modal-container">
            <GeneratedPasswordModal
              password={passwordResult}
              isOpen={modalOpen}
              toggleModal={this.toggleModal}
              modalTitle={
                intl.formatMessage({
                  id: 'components.generated-password-modal.reset.title',
                })
              }
              modalDescription={modalDescription()}
            />
          </div>
        }
        {
          showResetPasswordButton &&
          <ResetPasswordButton
            disabled={isDisabled}
            resetUserPassword={this.resetUserPassword}
          />
        }
        {
          showEnableUserToggle &&
          <EnableUserToggle
            userId={userId}
            isLocked={isLocked}
            isDisabled={isDisabled}
            editUser={this.props.editUserAction}
            resetUserPassword={this.resetUserPassword}
          />
        }
      </div>
    );
  }
}

AdminActions.propTypes = {
  intl: intlShape.isRequired,
  userId: PropTypes.string.isRequired,
  isLocked: PropTypes.bool.isRequired,
  isDisabled: PropTypes.bool.isRequired,
  editUserAction: PropTypes.func.isRequired,
  resetUserPasswordAction: PropTypes.func.isRequired,
  showResetPasswordButton: PropTypes.bool,
  showEnableUserToggle: PropTypes.bool,
};

AdminActions.defaultProps = {
  showResetPasswordButton: true,
  showEnableUserToggle: true,
};

const connectedComponent = connect(
  null,
  {
    editUserAction,
    resetUserPasswordAction,
  },
)(AdminActions);

export default injectIntl(connectedComponent);
