import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import Toggle from 'ui-library/lib/components/forms/form-toggle';
import DetailsTooltip from 'ui-library/lib/components/tooltips/DetailsTooltip';

class EnableUserToggle extends Component {
  constructor(props) {
    super(props);

    this.toggleConfirm = this.toggleConfirm.bind(this);
    this.statusIntlString = this.statusIntlString.bind(this);
    this.enableOrDisableUser = this.enableOrDisableUser.bind(this);
    this.enableAndUnlockUser = this.enableAndUnlockUser.bind(this);

    this.state = {
      showConfirm: false,
    };
  }

  toggleConfirm() {
    this.setState({ showConfirm: !this.state.showConfirm });
  }

  statusIntlString() {
    const { isLocked, isDisabled } = this.props;

    if (isLocked) {
      return 'user.status.locked';
    } else if (isDisabled) {
      return 'user.status.disabled';
    }

    return 'user.status.enabled';
  }

  updateData() {
    return { 'ds-pwp-account-disabled': [!this.props.isDisabled] };
  }

  enableOrDisableUser() {
    const {
      userId,
      isLocked,
      editUser,
    } = this.props;

    if (isLocked) {
      this.setState({ showConfirm: true });
      return;
    }

    editUser(userId, this.updateData());
  }

  async enableAndUnlockUser() {
    const {
      userId,
      editUser,
      resetUserPassword,
      isDisabled,
    } = this.props;
    this.toggleConfirm();

    try {
      if (isDisabled) await editUser(userId, this.updateData(), true);
      await resetUserPassword();
    } catch (error) {
      // Errors handled by actions
    }
  }

  render() {
    const { isLocked, isDisabled } = this.props;

    return (
      <div className="toggle-component">
        <span className="toggle-label">
          <FormattedMessage id={this.statusIntlString()} />
        </span>
        <Toggle
          stateless
          status={isLocked && Toggle.Status.LOCKED}
          toggled={!isDisabled && !isLocked}
          onToggle={this.enableOrDisableUser}
        />
        <DetailsTooltip
          stateless
          title="Title"
          positionClassName="alert bottom left"
          open={this.state.showConfirm}
          onToggle={this.toggleConfirm}
        >
          <FormattedMessage id="components.enable-user-toggle.tooltip-message" />
          <div className="button-group">
            <button className="primary" onClick={this.enableAndUnlockUser}>
              <FormattedMessage id="common.confirm" />
            </button>
            <button onClick={this.toggleConfirm}>
              <FormattedMessage id="common.cancel" />
            </button>
          </div>
        </DetailsTooltip>
      </div>
    );
  }
}

EnableUserToggle.propTypes = {
  isLocked: PropTypes.bool.isRequired,
  isDisabled: PropTypes.bool.isRequired,
  userId: PropTypes.string.isRequired,
  editUser: PropTypes.func.isRequired,
  resetUserPassword: PropTypes.func.isRequired,
};

export default EnableUserToggle;
