import { SESSION_TERMINATED, USER_EXPIRED, USER_FOUND } from 'redux-oidc';
import userManager from '../../../utils/userManager';
import { AUTHENTICATION, UNAUTHENTICATE_USER } from '../../actions/actionTypes';
import axios from '../../../utils/axiosInstance';
import { SUCCESS, FAILURE } from '../../actions';
import { logErrorToConsole } from '../../../utils/helpers';

const initialState = {
  user: undefined,
  error: undefined,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case USER_FOUND:
      setAxiosHeaders();
      return {
        ...state,
      };
    case UNAUTHENTICATE_USER:
    case SESSION_TERMINATED:
    case USER_EXPIRED:
      return {
        ...state,
        ...initialState,
      };
    case AUTHENTICATION[SUCCESS]:
      return reduceAuthenticationSuccess(action.data, state);
    case AUTHENTICATION[FAILURE]:
      return reduceAuthenticationFailure(action.error, state);
    default:
      return state;
  }
}

async function setAxiosHeaders() {
  try {
    const response = await userManager.getUser();
    axios.defaults.headers.common.Authorization = `${response.token_type} ${response.access_token}`;
  } catch (error) {
    logErrorToConsole(error);
  }
}

function reduceAuthenticationSuccess(data, prevState) {
  return {
    ...prevState,
    user: {
      ...data,
    },
    error: undefined,
  };
}

function reduceAuthenticationFailure(error, prevState) {
  return {
    ...prevState,
    user: undefined,
    error,
  };
}
