import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { injectIntl, intlShape } from 'react-intl';
import _ from 'lodash';
import Spinner from 'ui-library/lib/components/general/Spinner';
import Page from '../../containers/Page/Page';
import SearchBar from './SearchBar';
import SearchResults from './SearchResults';
import SortDropdown from './SortDropdown';
import {
  getSearchResults,
  clearSearchResultsAction,
  sortSearchResultsAction,
} from '../../store/actions/search/search';
import { filterResourceTypesAction } from '../../store/actions/resourceTypes/resourceTypes';
import permissions, { PERMISSIONS } from '../../utils/permissions/permissions';
import { MODEL_TYPES, PROP_MODEL_TYPES } from '../../utils/modelTypes';
import { NOT_FOUND_ROUTE } from '../../utils/routes';
import './SearchPage.css';
import { RESOURCE_TYPES } from '../../utils/resourceTypes';

export class SearchPage extends Component {
  constructor(props) {
    super(props);

    if (props.match.params.id === MODEL_TYPES.GROUPS && !props.groupsPermission) {
      props.history.replace(NOT_FOUND_ROUTE);
    }

    this.handleSortClick = this.handleSortClick.bind(this);
    this.searchQuery = this.searchQuery.bind(this);
    this.clearSearch = this.clearSearch.bind(this);
    this.displayGroupResults = this.displayGroupResults.bind(this);
    this.displayUserResults = this.displayUserResults.bind(this);
    this.onUserPage = this.onUserPage.bind(this);
    this.filterResourceTypes = this.filterResourceTypes.bind(this);

    this.state = {
      selectedOption: undefined,
      isLoading: false,
      initialGroupsSearch: false,
    };
  }

  componentDidMount() {
    const { match, groupsPermission } = this.props;
    const { dataType } = this.props.search;

    this.filterResourceTypes();
    if (
      match.params.id === MODEL_TYPES.GROUPS
      && groupsPermission
      && dataType !== MODEL_TYPES.GROUPS
    ) {
      this.searchQuery('', { skipMessage: true });
    }
  }

  componentDidUpdate(prevProps) {
    const {
      match,
      groupsPermission,
      history,
      search,
    } = this.props;

    if (match.params.id === MODEL_TYPES.GROUPS && !groupsPermission) {
      history.replace(NOT_FOUND_ROUTE);
    } else if (prevProps.match.params.id !== match.params.id && !search.queryString) {
      this.filterResourceTypes();
      if (match.params.id === MODEL_TYPES.GROUPS) {
        this.searchQuery('', { skipMessage: true });
      }
    }
  }

  onUserPage() {
    const { id } = this.props.match.params;

    return id === MODEL_TYPES.USERS;
  }

  filterResourceTypes() {
    const type = this.onUserPage()
      ? RESOURCE_TYPES.USER
      : RESOURCE_TYPES.GROUP;
    const filter = {
      byType: [type],
      byPermission: PERMISSIONS.READ,
    };
    this.props.filterResourceTypesAction(filter);
  }

  async searchQuery(queryString = '', options = {}) {
    const { skipMessage } = options;
    this.setState({
      selectedOption: undefined,
      isLoading: true,
      initialGroupsSearch: skipMessage,
    });

    try {
      await this.props.getSearchResults(queryString, { skipMessage });
    } catch (error) {
      // Errors handled by action
    }

    this.setState({ isLoading: false });
  }

  clearSearch() {
    this.props.clearSearchResultsAction();
  }

  displayGroupResults() {
    const {
      groupTitleAttribute,
      queryString,
      results,
      meta,
    } = this.props.search;

    if (results.length) {
      return (
        <SearchResults
          modelType={MODEL_TYPES.GROUPS}
          queryString={queryString}
          results={results}
          meta={meta}
          primarySearchAttribute={groupTitleAttribute}
          initialSearch={this.state.initialGroupsSearch}
        />
      );
    }

    return null;
  }

  displayUserResults() {
    const {
      results,
      queryString,
      meta,
      primarySearchAttribute,
    } = this.props.search;

    if (results.length) {
      return (
        <SearchResults
          modelType={MODEL_TYPES.USERS}
          queryString={queryString}
          results={results}
          meta={meta}
          primarySearchAttribute={primarySearchAttribute}
          onEnableToggle={this.enableOrDisableUser}
          onResetUserPasswordClick={this.resetUserPassword}
        />
      );
    }

    return null;
  }

  displaySort() {
    const { intl } = this.props;
    const {
      results,
      meta,
      primarySearchAttribute,
      groupTitleAttribute,
    } = this.props.search;
    const { isLoading } = this.state;

    if (results.length && !isLoading) {
      const displayName = this.onUserPage()
        ? (
          meta
          && meta.attributes[primarySearchAttribute]
          && meta.attributes[primarySearchAttribute].displayName
        )
        : (
          meta
          && meta.attributes[groupTitleAttribute]
          && meta.attributes[groupTitleAttribute].displayName
        );

      const options = [
        {
          label: intl.formatMessage({
            id: 'components.sort-dropdown.options.attr-asc',
          }, {
            displayName,
          }),
          value: 0,
        }, {
          label: intl.formatMessage({
            id: 'components.sort-dropdown.options.attr-desc',
          }, {
            displayName,
          }),
          value: 1,
        },
      ];

      return (
        <SortDropdown
          className="SearchControlItem SortDropdown"
          label={intl.formatMessage({ id: 'components.sort-dropdown.label' })}
          onSortClick={this.handleSortClick}
          options={options}
          selectedOption={this.state.selectedOption}
        />
      );
    }

    return null;
  }

  handleSortClick(selectedMenuItem) {
    const { primarySearchAttribute, groupTitleAttribute } = this.props.search;

    const sortBy = this.onUserPage()
      ? {
        0: { key: primarySearchAttribute, order: 'asc' },
        1: { key: primarySearchAttribute, order: 'desc' },
      }
      : {
        0: { key: groupTitleAttribute, order: 'asc' },
        1: { key: groupTitleAttribute, order: 'desc' },
      };
    this.setState({ selectedOption: selectedMenuItem });

    this.props.sortSearchResultsAction(sortBy[selectedMenuItem.value]);
  }

  render() {
    const { isLoading } = this.state;
    const { queryString, results } = this.props.search;
    const { intl, resourceTypes } = this.props;
    const { id } = this.props.match.params;
    const resultsClass = results.length || isLoading ? 'with-results' : 'no-results';
    const placeholderAttributes = searchAttributeNamesToString(resourceTypes.selected.attributes);
    const searchBarPlaceholder =
      intl.formatMessage({
        id: 'components.search-bar.placeholder',
      }, {
        type: _.capitalize(id),
        attributes: placeholderAttributes,
      });
    const pageTitle =
      intl.formatMessage({
        id: 'page.search.title',
      }, {
        type: _.capitalize(id),
      });

    const displayResults = this.onUserPage()
      ? this.displayUserResults()
      : this.displayGroupResults();

    return (
      <Page className="SearchPage" title={pageTitle}>
        <div className="SearchControls">
          <SearchBar
            showDropDown
            className={`SearchControlItem input-width-full ${resultsClass}`}
            submitSearch={this.searchQuery}
            clearSearch={this.clearSearch}
            placeholder={searchBarPlaceholder}
            queryString={queryString}
            isDisabled={isLoading}
            pageId={id}
          />
          {this.displaySort()}
        </div>
        <div>
          {
            isLoading
            ? <Spinner show />
            : displayResults
          }
        </div>
      </Page>
    );
  }
}

SearchPage.propTypes = {
  intl: intlShape.isRequired,
  search: PropTypes.shape({
    dataType: PropTypes.oneOf(PROP_MODEL_TYPES),
    queryString: PropTypes.string,
    results: PropTypes.arrayOf(PropTypes.shape({
      type: PropTypes.string.isRequired,
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }).isRequired),
    meta: PropTypes.shape({
      attributes: PropTypes.shape({}).isRequired,
    }),
    primarySearchAttribute: PropTypes.string,
    groupTitleAttribute: PropTypes.string,
  }),
  sortSearchResultsAction: PropTypes.func.isRequired,
  clearSearchResultsAction: PropTypes.func.isRequired,
  getSearchResults: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      id: PropTypes.string.isRequired,
    }).isRequired,
  }).isRequired,
  groupsPermission: PropTypes.bool.isRequired,
  history: PropTypes.shape({
    replace: PropTypes.func.isRequired,
  }).isRequired,
  auth: PropTypes.shape({
    meta: PropTypes.shape({
      attributes: PropTypes.shape({}).isRequired,
      included: PropTypes.shape({
        groups: PropTypes.shape({
          attributes: PropTypes.shape({}).isRequired,
        }),
      }),
    }),
  }).isRequired,
  resourceTypes: PropTypes.shape({
    selected: PropTypes.shape({
      attributes: PropTypes.shape({
        attributes: PropTypes.shape({}).isRequired,
      }).isRequired,
    }).isRequired,
  }).isRequired,
  filterResourceTypesAction: PropTypes.func.isRequired,
};

SearchPage.defaultProps = {
  search: {
    dataType: undefined,
    queryString: '',
    results: [],
    meta: undefined,
    primarySearchAttribute: undefined,
    groupTitleAttribute: undefined,
  },
};

function searchAttributeNamesToString(meta) {
  return meta && Object.values(meta.attributes)
    .filter(attr => attr.searchAttribute)
    .map(attr => attr.displayName)
    .join(', ');
}

function mapStateToProps(state) {
  const authMeta = state.authentication.user.meta;
  const {
    dataType,
    queryString,
    results,
    meta,
    primarySearchAttribute,
    groupTitleAttribute,
  } = state.search;

  return {
    auth: {
      meta: authMeta,
    },
    search: {
      dataType,
      queryString,
      results,
      meta,
      primarySearchAttribute,
      groupTitleAttribute,
    },
    groupsPermission: permissions.manageGroups(),
    resourceTypes: state.resourceTypes,
  };
}

export default injectIntl(connect(
  mapStateToProps,
  {
    getSearchResults,
    clearSearchResultsAction,
    sortSearchResultsAction,
    filterResourceTypesAction,
  },
)(SearchPage));
