import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { injectIntl, intlShape } from 'react-intl';
import Spinner from 'ui-library/lib/components/general/Spinner';
import _ from 'lodash';
import LeftNavBarActions from 'ui-library/lib/components/panels/left-nav/Actions';
import FormDropDownList from 'ui-library/lib/components/forms/FormDropDownList';

import { filterResourceTypesAction } from '../../store/actions/resourceTypes/resourceTypes';
import ModelForm from '../ModelForm/ModelForm';
import { PROP_MODEL_TYPES } from '../../utils/modelTypes';
import * as routes from '../../utils/routes';
import ResourceTypeDropDown from '../../containers/ResourceTypeDropDown/ResourceTypeDropDown';
import { PROP_RESOURCE_TYPES, getResourceById } from '../../utils/resourceTypes';
import { PERMISSIONS } from '../../utils/permissions/permissions';
import Page from '../Page/Page';
import { getTitleOrPrimaryAttributeValue } from '../../utils/helpers';

export class NewModelPage extends Component {
  constructor(props) {
    super(props);

    this.handleModelCreate = this.handleModelCreate.bind(this);
    this.handleDropDownChange = this.handleDropDownChange.bind(this);
    this.setDropDownOptions = this.setDropDownOptions.bind(this);

    this.state = {
      isLoading: true,
      isFinishedCreating: false,
      isSaving: false,
      createdModel: undefined,
      options: undefined,
      selectedOption: undefined,
    };
  }

  componentDidMount() {
    this.filterResourceTypes();
  }

  componentDidUpdate(prevProps) {
    // NOTE: We are ok to use setState here because of the conditional
    // https://reactjs.org/docs/react-component.html#componentdidupdate
    /* eslint-disable react/no-did-update-set-state */
    if (
      prevProps.selectedParentResource !== this.props.selectedParentResource
    ) {
      this.setDropDownOptions();
    }
  }

  setDropDownOptions() {
    const { data, meta } = this.props.selectedParentResource;

    if (!data || !meta) {
      this.setState({ options: undefined, selectedOption: undefined });
      return;
    }

    const options = data.map((item, index) => ({
      label: getTitleOrPrimaryAttributeValue(item.attributes, meta),
      value: index,
      id: item.id,
    }));

    this.setState({ options, selectedOption: options[0] });
  }

  async filterResourceTypes() {
    const { resourceType } = this.props;

    const filter = {
      byType: [resourceType],
      byPermission: PERMISSIONS.CREATE,
    };

    try {
      await this.props.filterResourceTypesAction(filter, true);
    } catch (error) {
      // Errors handled in actions, this is just to be safe.
    }

    this.setState({ isLoading: false });
  }

  async handleModelCreate(attrs) {
    const { selectedOption } = this.state;
    const parentId = _.get(selectedOption, 'id');
    this.setState({ isSaving: true });

    try {
      const res = await this.props.createAction(attrs, parentId);
      this.setState({ isFinishedCreating: true, isSaving: false, createdModel: res.data });
    } catch (e) {
      this.setState({ isSaving: false });
    }
  }

  handleDropDownChange(option) {
    this.setState({ selectedOption: option });
  }

  render() {
    const {
      intl,
      errorAttrs,
      meta,
      modelType,
      resourceType,
      selectedParentResource,
      selectedParentResourceTypeId,
      title,
    } = this.props;
    const {
      isFinishedCreating,
      isSaving,
      createdModel,
      isLoading,
      options,
      selectedOption,
    } = this.state;

    const parentError = selectedParentResource.error;
    const showSpinner = isLoading || selectedParentResource.isRequesting;
    const createdModelId = _.get(createdModel, 'data.id', undefined);
    const createdModelType = _.get(createdModel, 'data.type', undefined);
    let displayName = '';

    if (options && selectedParentResourceTypeId) {
      const parentResourceType = getResourceById(selectedParentResourceTypeId);
      displayName = _.get(parentResourceType, 'attributes.displayName', '');
    }

    if (isFinishedCreating && createdModelId && createdModelType) {
      return (
        <Redirect
          to={{
            pathname: routes.generatedRoute(modelType, {
              id: createdModelId,
            }),
            state: {
              id: createdModelId,
              type: createdModelType,
            },
          }}
        />
      );
    }

    return (
      <Page
        className="NewModelPage"
        title={title}
      >
        {
          showSpinner ?
            <Spinner show /> :
            <div className="page-body">
              <ResourceTypeDropDown
                showLabel
                getParent
                filter={{
                  byType: [resourceType],
                  byPermission: PERMISSIONS.CREATE,
                }}
              />
              {
                options &&
                options.length > 1 &&
                <FormDropDownList
                  required
                  label={intl.formatMessage({
                    id: 'containers.model-form.parent-dropdown-label',
                  }, {
                    displayName,
                  })}
                  labelPrompt={intl.formatMessage({ id: 'components.search-bar-drop-down.list-prompt' })}
                  searchType={FormDropDownList.SearchTypes.BOX}
                  options={options}
                  selectedOption={selectedOption}
                  onValueChange={this.handleDropDownChange}
                />
              }
              <ModelForm
                newModel
                meta={meta}
                isSaving={isSaving}
                onSave={this.handleModelCreate}
                errorAttrs={errorAttrs}
                parentResource={{
                  id: selectedParentResourceTypeId,
                  error: parentError,
                }}
                selectItemLeftNavAction={this.props.selectItemLeftNavAction}
              />
            </div>
        }
      </Page>
    );
  }
}

NewModelPage.propTypes = {
  intl: intlShape.isRequired,
  createAction: PropTypes.func.isRequired,
  errorAttrs: PropTypes.arrayOf(PropTypes.string),
  filterResourceTypesAction: PropTypes.func.isRequired,
  meta: PropTypes.shape({
    attributeCategories: PropTypes.arrayOf(PropTypes.string),
    attributes: PropTypes.shape({}).isRequired,
  }).isRequired,
  modelType: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  resourceType: PropTypes.oneOf(PROP_RESOURCE_TYPES).isRequired,
  selected: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({
      parentResourceType: PropTypes.string,
    }).isRequired,
  }).isRequired,
  selectedParentResource: PropTypes.shape({
    data: PropTypes.arrayOf(PropTypes.shape({})),
    meta: PropTypes.shape({}),
    error: PropTypes.string,
    isRequesting: PropTypes.bool,
  }),
  selectedParentResourceTypeId: PropTypes.string,
  selectItemLeftNavAction: PropTypes.func.isRequired,
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
};

NewModelPage.defaultProps = {
  errorAttrs: undefined,
  selectedParentResource: {
    data: [],
    meta: undefined,
    error: undefined,
    isRequesting: false,
  },
  selectedParentResourceTypeId: undefined,
  title: '',
};

function mapStateToProps(state) {
  const { selected } = state.resourceTypes;
  const selectedParentResourceTypeId = selected.attributes.parentResourceType;
  const { selectedParentResource } = state.resourceTypes;
  const meta = selected.attributes;

  return {
    meta,
    selected,
    selectedParentResource,
    selectedParentResourceTypeId,
  };
}

export default injectIntl(connect(
  mapStateToProps,
  {
    filterResourceTypesAction,
    selectItemLeftNavAction: LeftNavBarActions.selectItem,
  },
)(NewModelPage));
