import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import { Link, Redirect } from 'react-router-dom';
import Spinner from 'ui-library/lib/components/general/Spinner';

import DualColumnSearch from '../shared/dual-column-search/DualColumnSearch';
import { getGroupAction } from '../../store/actions/groups/groups';
import { searchGroupMembersAction, clearSearchResultsAction } from '../../store/actions/search/search';
import { HTTP_ERRORS, blacklistedDisplayAttrs, getTitleOrPrimaryAttributeValue } from '../../utils/helpers';
import { MODEL_TYPES } from '../../utils/modelTypes';
import * as routes from '../../utils/routes';

import './GroupProfilePage.css';
import Permission from '../../containers/Permission/Permission';
import { PERMISSIONS } from '../../utils/permissions/permissions';

export class GroupProfilePage extends Component {
  constructor(props) {
    super(props);

    this.searchQuery = this.searchQuery.bind(this);
    this.clearSearch = this.clearSearch.bind(this);
    this.renderAttributes = this.renderAttributes.bind(this);

    this.state = {
      isLoading: true,
      isLoadingContent: false,
    };
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getGroupAction(id);
  }

  componentDidUpdate(prevProps) {
    // NOTE: We are ok to use setState here because of the conditional
    // https://reactjs.org/docs/react-component.html#componentdidupdate
    /* eslint-disable react/no-did-update-set-state */
    if (this.props.group !== prevProps.group) {
      this.setState({ isLoading: false });
    } else if (this.props.groupMembers !== prevProps.groupMembers) {
      this.setState({ isLoadingContent: false });
    }
  }

  componentWillUnmount() {
    this.clearSearch();
  }

  searchQuery(queryString = '') {
    const { group, included } = this.props;
    this.setState({ isLoadingContent: true }, () => {
      this.props.searchGroupMembersAction(queryString, group.data.id, included);
    });
  }

  clearSearch() {
    this.props.clearSearchResultsAction({ subquery: true });
  }

  renderAttributes() {
    const { attributes } = this.props.group.data;
    const { groupMeta } = this.props;
    const displayAttrs = Object.keys(attributes)
      .filter(attr => blacklistedDisplayAttrs.indexOf(attr) < 0);

    if (displayAttrs.length > 1) {
      return (
        <Fragment>
          <div className="profile-attributes">
            {
              displayAttrs
                .map(attr => (
                  <div className="data-item" key={attr}>
                    <span className="data-item_label">
                      {groupMeta.attributes[attr].displayName}
                    </span>
                    <span>
                      {attributes[attr].join(', ')}
                    </span>
                  </div>
                ))
            }
          </div>

          <hr className="page-body_divider" />
        </Fragment>
      );
    }

    return null;
  }

  render() {
    const {
      error,
      group,
      groupMeta,
      included,
      groupMembers,
    } = this.props;
    const { isLoading, isLoadingContent } = this.state;

    if (error === HTTP_ERRORS[404]) return <Redirect to={routes.NOT_FOUND_ROUTE} />;
    if (!group || isLoading) return <Spinner show />;

    const groupTitle = getTitleOrPrimaryAttributeValue(group.data.attributes, groupMeta);

    return (
      <Permission
        requiredPermissions={[PERMISSIONS.MANAGE_GROUP_MEMBERSHIP]}
        resourceId={group.data.type}
      >
        <div className="GroupProfilePage">
          <div className="breadcrumbs">
            <Link to="/search/groups">
              <span className="inline-icon icon-dropdown-arrow" />
              <FormattedMessage id="breadcrumbs.page.search" />
            </Link>
          </div>

          <h1 className="title header-child">
            {groupTitle}
          </h1>

          <hr />

          <div className="page-body">

            {
              groupMeta
              && this.renderAttributes()
            }

            <Permission
              requiredPermissions={[PERMISSIONS.MANAGE_GROUP_MEMBERSHIP]}
              resourceId={group.data.type}
              redirect={false}
            >
              <DualColumnSearch
                profilePageModel={MODEL_TYPES.GROUPS}
                group={group}
                groupTitle={groupTitle}
                groupMeta={groupMeta}
                groupMembers={groupMembers}
                included={included}
                queryString={groupMembers && groupMembers.queryString}
                clearSearch={this.clearSearch}
                searchQuery={this.searchQuery}
                isLoadingSearch={isLoadingContent}
              />
            </Permission>
          </div>
        </div>
      </Permission>
    );
  }
}

GroupProfilePage.propTypes = {
  getGroupAction: PropTypes.func.isRequired,
  searchGroupMembersAction: PropTypes.func.isRequired,
  clearSearchResultsAction: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      id: PropTypes.string.isRequired,
    }).isRequired,
    path: PropTypes.string.isRequired,
  }).isRequired,
  group: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
      relationships: PropTypes.shape({
        members: PropTypes.shape({
          data: PropTypes.shape({
            directMembers: PropTypes.arrayOf(PropTypes.shape({})),
            indirectMembers: PropTypes.arrayOf(PropTypes.shape({})),
          }),
        }),
      }),
    }).isRequired,
  }),
  groupMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  groupMembers: PropTypes.shape({
    groupId: PropTypes.string.isRequired,
    queryString: PropTypes.string.isRequired,
    members: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    nonMembers: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    meta: PropTypes.shape({
      directMembers: PropTypes.shape({}),
      indirectMembers: PropTypes.shape({}),
      nonMembers: PropTypes.shape({}),
    }).isRequired,
    error: PropTypes.string,
  }),
  error: PropTypes.string,
  included: PropTypes.arrayOf(PropTypes.shape({})),
};

GroupProfilePage.defaultProps = {
  group: undefined,
  groupMeta: undefined,
  error: undefined,
  groupMembers: undefined,
  included: undefined,
};

function mapStateToProps(state) {
  const {
    error,
    groupMembers,
  } = state.search;

  return {
    error,
    groupMembers,
    group: state.group.group,
    included: state.group.included,
    groupMeta: state.group.meta,
  };
}

export default connect(
  mapStateToProps,
  {
    getGroupAction,
    searchGroupMembersAction,
    clearSearchResultsAction,
  },
)(GroupProfilePage);
