import React from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';
import Modal from 'ui-library/lib/components/general/Modal';
import FormTextField from 'ui-library/lib/components/forms/form-text-field';
import Button from 'ui-library/lib/components/buttons/Button';
import './GeneratedPasswordModal.css';

export const GeneratedPasswordModal = ({
  password,
  isOpen,
  toggleModal,
  intl,
  modalTitle,
  modalDescription,
}) => {
  if (isOpen) {
    return (
      <div className="GeneratedPasswordModal modal-container">
        <Modal
          className="generated-password-modal"
          expanded={isOpen}
          modalTitle={modalTitle}
          onClose={toggleModal}
        >
          <p>
            {modalDescription}
          </p>
          <FormTextField
            className="new-password"
            labelText={intl.formatMessage({ id: 'components.generated-password-modal.new-password.label' })}
            labelHelpText={intl.formatMessage({ id: 'components.generated-password-modal.new-password.label-help-text' })}
            stateless={false}
            value={password}
            maskValue
            showReveal
            readOnly
            flexWidth
          />
          <Button type="primary" onClick={toggleModal}>
            <FormattedMessage id="common.done" />
          </Button>
        </Modal>
      </div>
    );
  }

  return null;
};

GeneratedPasswordModal.propTypes = {
  intl: intlShape.isRequired,
  password: PropTypes.string.isRequired,
  isOpen: PropTypes.bool,
  toggleModal: PropTypes.func.isRequired,
  modalTitle: PropTypes.string,
  modalDescription: PropTypes.element,
};

GeneratedPasswordModal.defaultProps = {
  isOpen: false,
  modalTitle: '',
  modalDescription: undefined,
};

export default injectIntl(GeneratedPasswordModal);
