import * as types from '../../actions/actionTypes';

export const defaultState = {
  isDirty: false,
  isValid: false,
  currentAttrs: {},
  updatedAttrs: {},
};

export default function (state = defaultState, action) {
  switch (action.type) {
    case types.MODEL_FORM_SET:
      return {
        ...state,
        ...action.data,
      };
    default:
      return state;
  }
}
