import React from 'react';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import Spinner from 'ui-library/lib/components/general/Spinner';

import DualColumns from './DualColumns';
import SearchBar from '../../search/SearchBar';
import { PROP_MODEL_TYPES } from '../../../utils/modelTypes';

import './DualColumnSearch.css';

export const DualColumnSearch = (props) => {
  const {
    clearSearch,
    intl,
    queryString,
    searchQuery,
    profilePageModel,
    isLoadingSearch,
    dropDownProps,
  } = props;

  return (
    <div className="DualColumnSearch">
      <SearchBar
        className="SearchControlItem input-width-full constant-width"
        submitSearch={searchQuery}
        clearSearch={clearSearch}
        placeholder={intl.formatMessage({
          id: `components.dual-column-search.${profilePageModel}.search-prompt`,
        })}
        queryString={queryString}
        pageId={profilePageModel}
        {...dropDownProps}
      />

      {
        isLoadingSearch ?
          <Spinner show /> :
          <DualColumns {...props} />
      }
    </div>
  );
};

DualColumnSearch.propTypes = {
  intl: intlShape.isRequired,
  profilePageModel: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  group: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
      relationships: PropTypes.shape({
        members: PropTypes.shape({
          data: PropTypes.shape({
            directMembers: PropTypes.arrayOf(PropTypes.shape({})),
            indirectMembers: PropTypes.arrayOf(PropTypes.shape({})),
          }),
        }),
      }),
    }).isRequired,
  }),
  groupMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  groupMembers: PropTypes.shape({
    groupId: PropTypes.string.isRequired,
    queryString: PropTypes.string.isRequired,
    members: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    nonMembers: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    meta: PropTypes.shape({
      directMembers: PropTypes.shape({}),
      indirectMembers: PropTypes.shape({}),
      nonMembers: PropTypes.shape({}),
    }).isRequired,
    error: PropTypes.string,
  }),
  included: PropTypes.arrayOf(PropTypes.shape({})),
  queryString: PropTypes.string,
  clearSearch: PropTypes.func.isRequired,
  searchQuery: PropTypes.func.isRequired,
  userGroups: PropTypes.shape({
    queryString: PropTypes.string.isRequired,
    addableGroups: PropTypes.arrayOf(PropTypes.shape({})),
    currentGroups: PropTypes.arrayOf(PropTypes.shape({})),
    error: PropTypes.string,
  }),
  userMeta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.shape({}),
  }),
  user: PropTypes.shape({
    data: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }).isRequired,
    relationships: PropTypes.shape({
      groups: PropTypes.shape({
        data: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
      }),
    }),
  }),
  userName: PropTypes.string,
  groupTitle: PropTypes.string,
  isLoadingSearch: PropTypes.bool,
  dropDownProps: PropTypes.shape({
    showDropDown: PropTypes.bool,
    dropDownFilter: PropTypes.arrayOf(PropTypes.string),
    primaryDropDown: PropTypes.bool,
  }),
};

DualColumnSearch.defaultProps = {
  group: undefined,
  groupMeta: undefined,
  groupMembers: undefined,
  included: undefined,
  queryString: '',
  userGroups: undefined,
  userMeta: undefined,
  user: undefined,
  userName: '',
  groupTitle: '',
  isLoadingSearch: false,
  dropDownProps: undefined,
};

export default injectIntl(DualColumnSearch);
