import React from 'react';
import PropTypes from 'prop-types';
import AttributeCategoryItem from './AttributeCategoryItem/AttributeCategoryItem';
import ColumnLayout from '../../ColumnLayout/ColumnLayout';

export const AttributeCategoryList = ({
  categoryObjects,
  attributes,
}) => {
  if (!categoryObjects.length) return null;
  return (
    <ColumnLayout>
      {
        categoryObjects.map(category => (
          <AttributeCategoryItem
            key={category.name}
            category={category}
            attributes={attributes}
          />
        ))
      }
    </ColumnLayout>
  );
};

AttributeCategoryList.propTypes = {
  attributes: PropTypes.shape({}).isRequired,
  categoryObjects: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string.isRequired,
    attributes: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  }).isRequired).isRequired,
};

export default AttributeCategoryList;
