import React from 'react';
import _ from 'lodash';
import { FormattedMessage } from 'react-intl';
import LeftNavBarActions from 'ui-library/lib/components/panels/left-nav/Actions';
import endpoints from '../../../utils/apiEndpoints';
import * as types from '../actionTypes';
import { getUserAction } from '../userActions';
import {
  dispatchErrorMessage,
  dispatchSuccessMessage,
  generateHttpErrorMessage,
  logErrorToConsole,
} from '../../../utils/helpers';
import { MODEL_TYPES } from '../../../utils/modelTypes';
import * as routes from '../../../utils/routes';
import axios from '../../../utils/axiosInstance';
import { createAction, FAILURE, REQUEST, SUCCESS } from '../index';

export const groupsActions = {
  createGroupRequest: data => createAction(types.CREATE_GROUP[REQUEST], { data }),
  createGroupSuccess: data => createAction(types.CREATE_GROUP[SUCCESS], { data }),
  createGroupFailure: error => createAction(types.CREATE_GROUP[FAILURE], { error }),
};

export function getGroupAction(groupId) {
  return async (dispatch, getState) => {
    try {
      const resourceId = _.get(getState(), 'resourceTypes.selected.id');
      const endpoint = endpoints.endpointForResource(resourceId, { id: groupId });
      const res = await axios.get(endpoint);
      const { meta, included } = res.data;

      dispatch({
        type: types.GET_GROUP,
        data: {
          group: res.data.data,
          included,
          meta,
        },
      });
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { intlKey, statusCode } = errorMessage;

      dispatch({
        type: types.GET_GROUP_ERROR,
        data: {
          error: intlKey,
        },
      });

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            statusCode,
          }}
        />,
      );
    }
  };
}

export function createGroupAction(attributes, selectedParentId, skipMessage = false) {
  return async (dispatch, getState) => {
    try {
      const selected = _.get(getState(), 'resourceTypes.selected');
      const parentResourceType = _.get(selected, 'attributes.parentResourceType');
      const resourceId = _.get(selected, 'id');
      const endpoint = endpoints.endpointForResource(resourceId);
      const relationships = {
        orgs: {
          data: {
            type: parentResourceType,
            id: selectedParentId,
          },
        },
      };
      let payload = {
        attributes,
      };

      if (selectedParentId) {
        payload = {
          ...payload,
          relationships,
        };
      }

      dispatch(groupsActions.createGroupRequest({ payload }));

      const res = await axios.post(endpoint, {
        data: payload,
      });

      dispatch(groupsActions.createGroupSuccess({ group: res.data.data }));

      // Set the left nav bar to not be on "Create Group" item
      dispatch(LeftNavBarActions.selectItem(routes.GROUPS_SEARCH_ROUTE));

      if (!skipMessage) {
        dispatchSuccessMessage(
          dispatch,
          <FormattedMessage
            id="actions.create-group.success"
          />,
        );
      }

      return Promise.resolve(res);
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { intlKey, statusCode } = errorMessage;
      const regexp = /^\/data\/attributes\/(.*)/;
      const errorAttrs = [];

      if (statusCode === 422) {
        for (let i = 0; i < error.response.data.errors.length; i += 1) {
          const attr = regexp.exec(error.response.data.errors[i].source.pointer)[1];
          errorAttrs.push(attr);
        }
      }

      dispatch(groupsActions.createGroupFailure({ key: intlKey, errorAttrs }));

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            statusCode,
          }}
        />,
      );

      return Promise.reject(error);
    }
  };
}

export function addGroupMemberAction(group, members = [], profilePageModel = MODEL_TYPES.USERS) {
  return async (dispatch) => {
    try {
      const sendData = members.map(m => (
        {
          type: m.type,
          id: m.id,
        }
      ));
      const endpoint = endpoints.endpointForResource(group.type, {
        id: group.id,
        editMembers: true,
      });
      await axios.post(endpoint, {
        data: [...sendData],
      });

      dispatch({
        type: types.ADD_GROUP_MEMBER,
        data: {
          profilePageModel,
          members,
          group,
        },
      });

      dispatchSuccessMessage(
        dispatch,
        <FormattedMessage
          id="actions.member-added"
        />,
      );

      if (profilePageModel === MODEL_TYPES.GROUPS) {
        await dispatch(getGroupAction(group.id));
      } else if (profilePageModel === MODEL_TYPES.USERS && members.length === 1) {
        await dispatch(getUserAction(members[0].id, members[0].type));
      }
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { intlKey, statusCode } = errorMessage;

      dispatch({
        type: types.ADD_GROUP_MEMBER_ERROR,
        data: {
          error: intlKey,
        },
      });

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            statusCode,
          }}
        />,
      );
    }
  };
}

export function removeGroupMemberAction(group, members = [], profilePageModel = MODEL_TYPES.USERS) {
  return async (dispatch) => {
    try {
      const sendData = members.map(m => (
        {
          type: m.type,
          id: m.id,
        }
      ));
      const endpoint = endpoints.endpointForResource(group.type, {
        id: group.id,
        editMembers: true,
      });
      await axios.delete(endpoint, {
        data: {
          data: [...sendData],
        },
      });

      dispatch({
        type: types.REMOVE_GROUP_MEMBER,
        data: {
          profilePageModel,
          members,
          group,
        },
      });

      dispatchSuccessMessage(
        dispatch,
        <FormattedMessage
          id="actions.member-removed"
        />,
      );

      if (profilePageModel === MODEL_TYPES.GROUPS) {
        await dispatch(getGroupAction(group.id));
      } else if (profilePageModel === MODEL_TYPES.USERS && members.length === 1) {
        await dispatch(getUserAction(members[0].id, members[0].type));
      }
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { intlKey, statusCode } = errorMessage;

      dispatch({
        type: types.REMOVE_GROUP_MEMBER_ERROR,
        data: {
          error: intlKey,
        },
      });

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            statusCode,
          }}
        />,
      );
    }
  };
}
